<template>
  <div id="detail">
    <alert v-if="!showD" :class="named" :message="dataMessage"></alert>
    <div v-if="showD">
      <router-link to="/"><img class="right_p" src="../../static/assets/close.jpg" width="24"></router-link>
      <p class="data">{{name | setDate}}</p>
      <h2>{{newsDetail.imgTitle}}</h2>
      <p class="contentDes">{{newsDetail.dateTime}}</p>
      <p class="contentDes"><img :src="newsDetail.dateImg"></p>
      <a :href="newsDetail.url" target="_blank">
        <button>资料来源</button>
      </a>
    </div>
  </div>
</template>

<script>
  import alert from './alert'
  import {localData} from '@/common/localData'
  export default {
    name: 'detail',
    props: ['dataJson'],
    components:{
      alert,
    },
    data() {
      return {
        showD:false,
        named:'',
        id: this.$route.params.id,
        name: this.$route.query.name,
        newsDetail: {},
      }
    },
    filters:{
      setDate(value){
        console.log(value)
        return value.slice(0,2)+'月'+value.slice(2)+'日'
      }
    },
    methods:{
      loadData(key){
        this.named='success';
        this.dataMessage='数据正在加载中……';
        this.$ajax.get('./../static/data_'+this.name+'.json')
          .then(
          data=>{
            localData.set(data.data,key)
            this.showD=true;
            return localData.get('todayFresh'+this.name)
          }
        ).catch(data=>{this.named='error';this.showD=false;this.dataMessage='数据加载失败……';})
          .then(
          data=>{
            return this.newsDetail = data.news.data[this.id - 1]
          }
        )
      }
    },
    created() {
      this.showD=true;
        if(!localData.get('todayFresh'+this.name)){
           return this.loadData('todayFresh'+this.name);
        }
        var data=localData.get('todayFresh'+this.name)
        this.newsDetail = data.news.data[this.id - 1]
    }
  }
</script>

<style scoped>
 .contentDes img{ width:100%}
  .right_p {
    position: fixed;
    top: 20px;
    right: 10px;
  }

  #detail .data {
    padding: 6% 0 3%;
    font-size: 12px;
    color: #8b8b8b;
  }

  h2 {
    width: 95%;
    line-height: 1.3
  }

  #detail {
    margin: 0 auto;
    width: 90%;
  }

  button {
    background: #0384e1;
    border-radius:16px;
    width:88px;
    height:30px;
    color: #fff;
    border: 0;
    line-height:30px;
    position: fixed; bottom: 20px; left: 5%;
  }

  .contentDes {
    margin-top: 20px;
    color: #888
  }
</style>



