function sessionStorageData() {
  this.get = (item='todayFresh') => {
    return sessionStorage.getItem(item) ? JSON.parse(sessionStorage.getItem(item)) : '';
  }
  this.set = (obj,item='todayFresh') => {
    sessionStorage.setItem(item, JSON.stringify(obj));
  }
  this.clear = (item='todayFresh') => {
    sessionStorage.removeItem(item);
  }
}
const localData = new sessionStorageData()
export {localData}



