<template>
  <div class="alert" :class="[named]">
    {{message}}
  </div>
</template>
<script>
  export default {
    name: 'alert',
    data () {
      return {
      }
    },
    props:['message','named']
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .alert {
    padding: 15px;
    color: #a94442;
    margin-bottom: 20px;
    border: 1px solid transparent;
    border-radius: 4px;
    width:80%; margin: 0 auto 20px;
    margin-top:20px ;
  }
  .error{
    background-color: #f2dede;
    border-color: #ebccd1;
    color: #a94442;
  }
  .success{
    color: #3c763d;
    background-color: #dff0d8;
    border-color: #d6e9c6;
  }
</style>



