<template>
  <div class="mainContent">
    <alert v-if="!showD" :class="named" :message="dataMessage"></alert>
    <div v-if="showD">
      <date-content :data="data"></date-content>
      <new-content :dataJson="dataJson" :data="data.news" dataClass="new"></new-content>
      <img-content v-if="data.ad" :data="data.ad"></img-content>
      <new-content v-if="!m_show" :data="data.products" :class="{'m_top':m_show}"></new-content>
    </div>
  </div>
</template>

<script>
  import dateContent from './dateContent'
  import newContent from './newContent'
  import imgContent from './imgContent'
  import alert from './alert'
  import {localData} from '@/common/localData'
//  import {changeData} from '@/common/dataChange'
  export default {
    name: 'mainContent',
    props:['dataJson'],
    components:{
      'date-content':dateContent,
      'new-content':newContent,
      'img-content':imgContent,
      alert,
    },
    data () {
      return {
        data:{
          news:'',
          products:'',
          ad:'',
        },
        named:'',
        dataMessage:'',
        showD:false,
        m_show:true,
        dataNum:this.dataJson
      }
    },
    methods:{
      loadData(key){
        console.log(1)
        this.named='success';
        this.dataMessage='数据正在加载中……';
        this.$ajax.get('./../static/data_'+this.dataNum+'.json')
          .then(
            data=>{
//              let dataImg=data.data;
//              var value=changeData(dataImg);
              localData.set(data.data,key)
              this.showD=true;
              return localData.get('todayFresh'+this.dataNum)
            }
          ).catch(data=>{this.named='error';this.showD=false;this.dataMessage='数据加载失败……';})
          .then(
            data=>{
              return this.data = data
            }
          )
      }
    },
    // updated(){
    //   this.loadData('todayFresh'+this.dataNum)
    // },
    created(){
     this.showD=true;
     // this.loadData('todayFresh'+this.dataNum)
      if(!localData.get('todayFresh'+this.dataNum)){
        return this.loadData('todayFresh'+this.dataNum);
      }
      var data=localData.get('todayFresh'+this.dataNum)
      this.data=localData.get('todayFresh'+this.dataNum);
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #listcontent .alert{ display: none}
  .mainContent{ margin-bottom:5px;}
  #listcontent .mainContent:nth-of-type(1) .alert{ display: block}
</style>



