<template>
  <div id="img-content">
    <ul>
      <li v-if="datamg[0].dateImg" v-for="item in data" class="content m_top imgRond"><a :href="item.url"><img :src="item.dateImg"></a></li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'img-content',
  props:['data'],
  data(){
    return{
      datamg:this.data
    }
  }
}
</script>

<style scoped>
li{ overflow: hidden}
</style>



