<template>
  <div id="date-content">
    <div class="content cont" :class="dataClass">
      <div class="tit">
        <p class="tip">{{data.tipsD}}</p>
        <h2>{{data.title}}</h2>
      </div>
      <dl v-for="item in data.data">
        <router-link :to="{path:/detail/+item.id,query:{name:dataJson}}">
          <dt v-if="item.dateImg.length"><img v-lazy="item.dateImg" width="50" height="50"></dt>
          <dd class="border_line">
            <div class="f1">
              <h3>{{item.imgTitle | newtitle}}</h3>
              <p class="date">{{item.dateTime | newDate}}</p>
            </div>
            <div class="f2" v-if="item.price">
              <span>{{item.price}}<b><img :src="item.priceImg" width="7"></b></span>
            </div>
          </dd>
        </router-link>
      </dl>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'app',
    props: ['data', 'dataClass', 'dataJson'],
    filters: {
      newDate(value) {
        if (value.length > 15) {
          return value.slice(0, 15) + "……"
        }
        return value
      },
      newtitle(value) {
        if (value.length > 14) {
          return value.slice(0, 14) + "……"
        }
        return value
      }
    }
  }
</script>

<style scoped>
  #app .new dl dd {
    text-indent: 0;
  }

  #app .new dl dd p {
    margin-top: 5px;
    text-indent: 0;
  }

  #app .content dl dt {
    height: 50px;
    width: 50px;
    border-radius: 8px;
  }
</style>



